import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import(/* webpackChunkName: "about" */ '../components/Login.vue')
  }, {
    path: '/NavMenuView',
    name: 'NavMenuView',
    component: () => import('../components/NavMenuView.vue'),
    children: [
      {
        path: '/about',
        name: 'about',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
      }, {
        path: '/UserData',
        name: 'userdata',
        component: () => import('../components/UserTabView.vue')
      }, {
        path: '/FileData',
        name: 'filedata',
        component: () => import('../components/FileDataView.vue')
      }, {
        path: '/AddUserData',
        name: 'adduserdata',
        component: () => import('../components/AddUserData.vue')
      },{
        path: '/ThisUserDataUI',
        name: 'ThisUserData',
        component: () => import('../components/ThisUserData.vue')
      },{
        path: '/',
        name: 'index',
        component: () => import('../components/HelloWorld.vue')
      },{
        path: '/UpLoad',
        name: 'UpLoad',
        component: () => import('../components/UpLoad.vue')
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router
